import React, { useRef, useState } from 'react'
import Layout from '../components/layout'
import trtData from '../content/trt-data.json'
import GetInTouch from '../components/get-in-touch'
import { Link } from 'gatsby'
import { useForm } from "react-hook-form";
import { compileFunction } from 'vm'
import Metatags from '../components/Metatags'

const CareerPage = () => {

    const { register, handleSubmit, reset, errors, getValues, formState: {
        isSubmitting
    } } = useForm();

    let base64File = useRef(null)

    const [fileName, setFileName] = useState('');
    const [isSubmited, setIsSubmited] = useState(false);

    const hasError = field => errors[field] !== undefined;
    const getError = field => hasError(field) && errors[field].message;

    const onChangeHandler = event => {
        base64File = _convertImageIntoBase64(event.target.files[0]);
        setFileName(event.target.files[0].name)
    }
    const API_URL = process.env.CAREER_API_URL;
    const onSubmit = async (data) => {
        try {
            setIsSubmited(true)
            data.fileName = fileName
            const payload = JSON.parse(JSON.stringify(data))
            reset();
            await fetch(API_URL, {
                method: 'POST',
                mode: 'cors',
                cache: 'no-cache',
                body: JSON.stringify(payload),
                headers: {
                    'Content-type': 'application/json; charset=UTF-8',
                },
            });
            setIsSubmited(false)
            setFileName('')
        } catch (error) {
            setIsSubmited(false)
            // handle server errors
        }
    };

    const validateEmail = _ => {
        const values = getValues({ nest: true });
        const re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        return (re.test(values.email.toLowerCase()) || 'Please enter valid email');
    }
    const validateFile = _ => {
        const values = getValues({ nest: true });
        const supportedFileTypes = ['pdf', 'doc', 'docx'];
        const fileExtension = values.resumesDetails[0].name.substr(values.resumesDetails[0].name.lastIndexOf('.') + 1);
        if (!supportedFileTypes.includes(fileExtension)) {
            return 'Please upload PDF or DOC file';
        } else if (values.resumesDetails[0].size / 1000 > 10000) {
            return 'File size should be less then 10MB';
        }
    }

    function _convertImageIntoBase64(file) {
        const reader = new FileReader();
        reader.onloadend = e => {
            const readerDetails = e.target;
            const base64result = readerDetails.result.substr(
                readerDetails.result.indexOf(',') + 1,
            );
            file.base64 = base64result;
        };
        reader.readAsDataURL(file);
        return file
    }


    return (
        <Layout>
            <section className="trt-career">
                <div className="career-banner">
                    <div className="container">
                        <div className="row align-items-center justify-content-between">
                            <div className="col-lg-7">
                                <h1>Career At Third Rock Techkno</h1>
                                <p>We’re looking for people to join the team who are as excited as we are to help organization
                                build
                                new web
                                applications and platforms. If you are looking for an opportunity where you can learn,
                                explore,
                                develop
                                along with the fun times, good food and ofcourse Chai then you will love working with us. Do
                                apply
                                and come
            to experience the coolest work atmosphere.</p>
                            </div>
                            <div className="col-lg-5">
                                <div className="hire-career-form">
                                    <form method="post" onSubmit={handleSubmit(onSubmit)}>
                                        <div className="group mt-0">
                                            <input type="text"
                                                name="name"
                                                disabled={isSubmitting}
                                                id="name"
                                                ref={register({
                                                    required: 'Please enter first name',
                                                })}
                                                placeholder="Your Name*"
                                            />
                                            <span className="highlight"></span>
                                            <span className="bar"></span>
                                            {/* <label>Your Name*</label> */}
                                            {hasError("name") && (
                                                <span className="form-error" error={toString(hasError("name"))}> {getError("name")}</span>
                                            )}
                                        </div>
                                        <div className="group">
                                            <input type="text"
                                                disabled={isSubmitting}
                                                name="email"
                                                id="email"
                                                ref={register({ validate: validateEmail, required: "Please enter email" })}
                                                placeholder="Email Address*"
                                            />
                                            <span className="highlight"></span>
                                            <span className="bar"></span>
                                            {/* <label>Email Address*</label> */}
                                            {hasError("email") && (
                                                <span className="form-error" error={toString(hasError("email"))}> {getError("email")}</span>
                                            )}
                                        </div>
                                        <div className="group">
                                            <textarea type="textarea"
                                                rows="3"
                                                name="description"
                                                disabled={isSubmitting}
                                                id="description"
                                                ref={register({ required: "Please enter description" })}
                                                placeholder="Description*"
                                                ></textarea>
                                            <span className="highlight"></span>
                                            <span className="bar"></span>
                                            {/* <label>Description*</label> */}
                                            {
                                                hasError("description") && (
                                                    <span className="form-error" error={toString(hasError("description"))}>
                                                        {getError("description")}
                                                    </span>
                                                )
                                            }
                                        </div>
                                        <div className="group">
                                            <input
                                                type="file"
                                                accept="application/pdf"
                                                multiple={false}
                                                name="resumesDetails"
                                                id="resumesDetails"
                                                onChange={onChangeHandler}
                                                ref={register({ required: "Please upload a file", validate: validateFile })}
                                            />
                                            {
                                                hasError("resumesDetails") && (
                                                    <span className="form-error" error={toString(hasError("resumesDetails"))}>
                                                        {getError("resumesDetails")}
                                                    </span>
                                                )
                                            }
                                            <div className="selected-file-name">{fileName}</div>
                                        </div>
                                        <div className="text-right">
                                            <button
                                                disabled={isSubmitting}
                                                type="submit"
                                                className="btn btn-trt-primary">
                                                {!isSubmited ? <span>Send Inquiry</span> : ''}
                                                {isSubmited ? <div
                                                    className="spinner-border text-light"
                                                    role="status">
                                                    <span className="sr-only">Loading...</span>
                                                </div> : ''}
                                            </button>
                                        </div>
                                    </form>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="bg-gradient">
                    <div className="container">
                        <div className="current-openings">
                            <h3>Current Openings</h3>
                            <div className="opening-box">
                                <h5>Hybrid mobile developer (React native experience) - <span>2-3 Years (25K to 40K p/m)</span></h5>
                                <p>Technology:- React Native, Angular 2,4, React, HTML, JavaScript knowledge</p>

                                <label>Description</label>
                                <ul>
                                    <li>We are looking for a highly motivated Hybrid mobile developer who will have 1 to 2 years of
              experience in React Native.</li>
                                    <li>Candidate should have good knowledge in mobile development and should have a good portfolio
              to showcase.</li>
                                    <li>Your responsibility will be Project understanding, developing app with the best quality
              code.</li>
                                    <li>Helping the technical team in project assessment (timeline, quotation, etc).</li>
                                    <li>Good Analytical and Problem Solving abilities.</li>
                                    <li>Ability to work on Multiple Projects with multiple deadlines.</li>
                                </ul>
                            </div>
                            <div className="opening-box">
                                <h5>Angular JS developer - <span>1-2 Years (15K to 35K p/m)</span></h5>
                                <p>Technology:- AngularJS 1.x, Angular 2/4/5, HTML, JavaScript knowledge</p>

                                <label>Description</label>
                                <ul>
                                    <li>As a software engineer, you have to be an expert in the modern JavaScript MVC framework - AngularJS,
                                    Angular
              v2.0+.</li>
                                    <li>Hands-on with implementing complex Angular applications, directives, controllers, services.</li>
                                    <li>Strong expertise in HTML, CSS, and writing cross-browser compatible code.</li>
                                    <li>Core web fundamentals: HTML, JavaScript, and CSS. Experience with RESTful services.</li>
                                    <li>JavaScript frameworks with JS-based build tools like Grunt, Gulp.</li>
                                    <li>You have to be quick in problem solving, good enough in logical work and have to be passionate about
              work.</li>
                                </ul>
                            </div>
                            <div className="opening-box">
                                <h5>NodeJS developer - <span>1-2 Years (15K to 40K p/m)</span></h5>
                                <p>Technology:- NodeJS, ExpressJS, Type Script, JavaScript Knowledge, AWS Lambda, API gateway and other AWS
            services</p>
                                <label>Description</label>
                                <ul>
                                    <li>In depth knowledge of NodeJs, ExpressJs is mandatory.</li>
                                    <li>Experience with AWS, MongoDB, PostgreSQL, MySQL is a big plus.</li>
                                    <li>Build customer-facing UI and back-end services for payment processing administration and management
              Code, taste and operate nodeJs based services.</li>
                                    <li>If you are an analytical problem solver with a strong NodeJS knowledge and a passion for teamwork and
              innovation, we have the perfect job for you!</li>
                                </ul>
                            </div>
                            <div className="opening-box">
                                <h5>React Developer - <span>2.5-3.5 Years (15K to 40K p/m)</span></h5>
                                <p>Technology:- ReactJS, Type Script, JavaScript Knowledge</p>
                                <label>Description</label>
                                <ul>
                                    <li>As a React Native developer you must have extensive React Native development experience for Front-end
                                    using Node.js as Backend (More focus on React Native),ES6, REST API implementation and integration using
              Redux, PostgreSQL, MongoDB, MySQL as database.</li>
                                    <li>Candidate has to be passionate and self-learner.</li>
                                    <li>Good Analytical and Problem Solving abilities.</li>
                                    <li>Ability to work on Multiple Projects with multiple deadlines.</li>
                                </ul>
                            </div>
                            <div className="opening-box">
                                <h5>Android Developer - <span>3-3.5 Years (25K to 40K p/m)</span></h5>
                                <p>Technology:- Kotlin, Flutter, java, AndroidSDK</p>
                                <label>Description</label>
                                <ul>
                                    <li>BS/MS degree in Computer Science, Engineering or a related subject.</li>
                                    <li>Proven software development experience and Android skills development.</li>
                                    <li>Proven working experience in Android app development and have published at least one original Android
              app.</li>
                                    <li>Experience with Android SDK Experience working with remote data via REST and JSON.</li>
                                    <li>with third-party libraries and APIs working knowledge of the general mobile landscape, architectures,
              trends, and emerging technologies.</li>
                                    <li>Solid understanding of the full mobile development life cycle.</li>
                                    <li>Should aware of GIT usage flow.</li>
                                    <li>MUST aware of Multithreading and background tasks.</li>
                                    <li>Good communication skills.</li>
                                </ul>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        </Layout>
    )
}

export default CareerPage